import axios from '@/utils/request'

// 获取地址列表

export const getAddressList = () => {
  return axios.get('/address/list')
}

// 获取默认收货地址

export const getDefaultAdd = () => {
  return axios.get('/address/defaultId')
}

// 获取省市区数据

export const getArea = () => {
  return axios.get('/region/tree')
}
