import axios from '@/utils/request'

// 订单结算
// mode: cart 只用传 obj.cartIds  mode: buyNow 则需要传 obj下的 goodsId goodsName goodsSkuId
export const checkOrder = (mode, obj) => {
  return axios.get('/checkout/order', {
    params: {
      delivery: 10,
      couponId: 0,
      isUsePoints: 0,
      mode,
      ...obj
    }
  })
}

// 提交订单
export const orderSubmit = (mode, obj) => {
  return axios.post('/checkout/submit', {
    mode,
    delivery: 10, // 10快递配送 20 自提
    couponId: 0, // 优惠券
    isUsePoints: 0, // 不使用积分
    payType: 10, // 余额支付
    ...obj
  })
}

// 订单列表
export const getMyOrderList = (dataType, page) => {
  return axios.get('/order/list', {
    params: {
      dataType,
      page
    }
  })
}
